@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);

body{
  display: flex;
  flex-direction: column;
  background-color: #b8eeee;
  background-repeat: no-repeat;
  background-position: bottom ;
  background-attachment: fixed;
  background-size: cover;
  max-height: 100vh;
  margin: 0;
  /* justify-content: center; */
}

button{
  cursor:pointer
}
 a{
  -webkit-text-decoration: "none";
          text-decoration: "none";
  color: "inherit";
 }

h1{
  font-family: 'Abril Fatface', cursive;      
  font-size: 5rem;
  justify-items: center;
}
h3{
  padding: 0px;
  margin: 0px;
}

h4{
  padding: 0px;
  margin: 0px;
}
.Landing {
  display: flex;
  flex-direction: row;
  color: #494949;
  text-align: center;
  justify-content: center;
  align-items: flex-start;
  border: #494949;
}

.Title {
  font-family: "Abril Fatface", cursive;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self:center;
  align-content: center;
  width: 530px;

}

.dogPic {
  display: flex;
  align-items: stretch;
  max-width: 100%;
  max-height: 100%;
  position: absolute bottom 0;
  margin: 0;
}
.EnterSite {
  border-radius: 7px;
  font-family: Arial;
  color: #d5f9fa;
  font-size: 36px;
  background: #494949;
  padding: 11px 20px 10px 20px;
  text-decoration: none;
}

.EnterSite:hover {
  color: #494949 !important;
  background: #9ac6f3;
  border-color: #b8d3ee !important;
  text-transform: uppercase;
  transition: all 0.2s ease 0s;
  border-radius: 1rem;
}

.AddBreed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #b8d3ee;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  margin: 1% 30% 1% 30%;
  padding: 1em;
  max-width: 40em;
}

input[type="text"],
select,
textarea {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
}
input[type="number"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
}
.addTitle {
  font-size: 24px;
  margin: 0.5em;
  padding: 0;
}

label {
  padding: 12px 12px 12px 0;
  display: inline-block;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 12px;
  font-weight: bold;
}

input[type="submit"] {
  margin: 1px;
  background-color: #494949;
  color: white;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}

.MinMax {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.TemperContainer {
  display: flex;
  margin: 0.5em;
  flex-wrap: wrap;
  padding: 0.1em;
}
.IndividualTemper {
  background-color: #494948;
  padding: 0.3em;
  margin: 0.1em;
  border-radius: 0.4em;
  font-family: sans-serif;
  color: #e1f8f8;
}

.DelTemper {
  border-radius: 1em;
  margin: 0.1px 2px;
  color: #ffffff;
  border: none;
  background: #494949;
  padding: 3px 6px 3px 6px;
  text-decoration: none;
}
.DelTemper:hover {
  color: #ff0606;
  text-decoration: none;
}

.error {
  margin: 0;
  color: #db3659;
  font-size: 10px;
  align-self: center;
  text-align: justify;
}

.ExternalCard {
  background-color: #b8d3ee;
  -webkit-text-decoration: "none";
          text-decoration: "none";
  border-radius: 0.3vw;
  width: 20vw;
  border: .1em solid #b8d3ee;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-content: center;
  text-align: center;
  padding: 0.1vw;
  margin: 0.2vw;
  height: 42vh;
  flex-grow: 2;
}
.ExternalCard:hover{
  -webkit-text-decoration: "none";
          text-decoration: "none";
  border-radius: 0.3vw;
  width: 20vw;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-content: center;
  text-align: center;
  padding: 0.1vw;
  margin: 0.2vw;
  height: 41vh;
  flex-grow: 2;
  background-color: #9ac6f3;
  height: 42vh;
  border: .1em solid #96eaec;
}
.CardTitle{
  margin: .21em auto 0.5em;
}
.DogImg {
  align-self: center;
  border-radius: 0.2em;
  width: 200px;
  height: 100px;
  padding: .51em;
  -webkit-text-decoration: "none";
          text-decoration: "none";
}

.Temp {
  background-color: #494949;
  color: #b8eed3 ;
  text-decoration: none;
  margin: 0.3em;
  padding: 0.3em;
  border-radius: 1em;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.AllTemps {
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0.3em;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
}

a {
  text-decoration: none;
  color: #494949;
}

h3{
    padding: 0px;
    margin: 0px;
    font-family: 'Abril Fatface', cursive;
}

p{
    padding: 0;
    margin: 0;
}
.Breeds{
display: flex;
flex-direction: row;
flex-wrap: wrap;
justify-content: space-around;
}
.TopBar {
  display: flex;
  flex-direction: row;
  background: #cdf3f3;
  justify-content: center;
  align-items: center;
  padding: 0.5em 1em;
}

.Filter {
  padding: 0em 1em;
  display: flex;
  flex-direction: row;
}

.Order {
  padding: 0em 1em;
  display: flex;
  flex-direction: row;
}

.Search {
  padding: 0em 1em;
  display: flex;
  flex-direction: row;
}

.Reset {
  margin: 1px;
  background-color: #494949;
  color: white;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}
.Numbers {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Num {
  margin: 2px;
  background-color: #494949;
  color: #b8d3ee;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: left;
  width: 3em;
}

.Num:hover {
  background: #b8d3ee;
  color: #494949;

  text-decoration: none;
}
.selectedPage {     
  margin: 2px;
  background-color: #a4cbf3;
  color: #494949;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: left;
  width: 3em;

}
.Reset:hover {
  background: #eeb8b8;
  color: #494949;
}
.Details {
  background-color: #b8d3ee;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1vw;
  padding: 1vw;
  border-radius: 1vw;
  max-width: 70%;
  overflow: hidden;

}

.Image {
  width: 40vw;
  height: 40vh;
  background: transparent no-repeat center;
  background-size: cover;
  overflow: hidden;
}


.rightSide {
  width: 40vw;
  height: 40vh;  padding: 1em;
  margin: 2em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  text-align: center;
  align-content:center;
  overflow: hidden;

}

.Info {
  display: flex;
  flex-direction: row;
}

.DetailTempers{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 15em;
}
.showTemps{
  display: flex;
  flex-direction: row;
  margin: 2em;
}
.BreedName {
  font-family: "Abril Fatface", cursive;
  font-size: 2em;
  margin: 0;
  color:#494949 ;

}
.Col{
  margin: 1rem;
}
h4 {
  font-family: "Abril Fatface", cursive;
  font-size: 1.5em;
  color:#494949 ;

}

p {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 0.8em;
  color: #374747;
  font-weight: bold;

}


.Nav {
  background: #e1f8f8;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-shadow: 2px 5px 16px #888888;
}
.Icon1 {
  width: 50px;
}
.Icon2 {
  width: 30px;
}
.Logo {
  font-family: "Abril Fatface", cursive;
  font-size: 40px;
  display: flex;
  flex-direction: row;
}

.Side {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0.2em;
}

.Left {
  padding: 1em;

}

.Right{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    padding: 0.2em;
    margin: 0.5em 0.5em;
    width: 4em;
}
h5 {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

h6 {
  margin: 0;
  padding: 0;
}

.surprisebtn {
    border-radius: 7px;
    color: #b8d3ee;
    font-size: 24px;
    background: #494949;
    padding: 11px 20px 10px 20px;
    text-decoration: none;
    font-family: 'Abril Fatface', cursive;      
    margin-bottom: 11px;
  }
  
  .surprisebtn:hover {
    color: #494949 !important;
    background: #b8d3ee;
    text-transform: uppercase;
    transition: all 0.2s ease 0s;
    font-family: 'Abril Fatface', cursive;   
    border-color: #494949;   
  }
  
